import axios from 'axios'
import q from 'q';

let api = 'http://101.201.57.91:2025/keyword';

function splitWord(content, num) {
    content = content || '';
    return axios({
        url: api,
        method: 'post',
        timeout: 5000,
        data: JSON.stringify({content: content, key_num: num}),
        headers: {
            'Content-Type': 'application/json'
        }
    }).then(response => {
        let res = response.data;
        try {
            res = JSON.parse(res);
        } catch (error) {}
        if (res.code != 0) {
            throw new Error(res.message || '未知原因');
        } else {
            return res.keywords;
        }
    });
}

export default splitWord;


