import Uploader from '../widget/Uploader';
class CoverUploader {
    constructor({
        token,
        serverPath,
        vm,
        pick = '#filePicker',
        isEdit = false
    }) {
        this.vm = vm;
        let coverUploader = new Uploader({
            auto: false,
            paste: "#J-image-list",
            server: serverPath,
            fileVal: "uploadFile",
            pick: pick,
            fileNumLimit: 3,
            formData: {
                token
            },
            accept: {
                title: 'Images',
                extensions: 'gif,jpg,jpeg,bmp,png',
                mimeTypes: 'image/png,image/jpg,image/jpeg,imge/bmp'
            }
        }, {
            'fileQueued': (uploaderInstance, file) => {
                uploaderInstance.makeThumb(file, (error, src) => {
                    if (error) {
                        util.showError('创建二次分享本地缩略图失败');
                        throw error;
                    }
                    vm.coverPreviewThumbs.push({
                        id: file.id,
                        name: file.name,
                        src: src
                    });
                    vm.form.cover_show_type = this.getShowType();
                }, 100, 100);
            }
        });
        this.uploader = coverUploader;
    }
    init() {
        this.vm.coverPreviewThumbs.forEach((item) => {
            this.uploader.queue.add(item.src, item.src);
        });
    }
    getValidFiles() {
        let {
            uploaderInstance
        } = this.uploader;
        let result = [];
        let status = ["inited", "queued", "complete", "error", "interrupt"];
        status.forEach((state) => {
            [].push.apply(result, uploaderInstance.getFiles(state));
        });
        return result;
    }
    getInvalidCoverCount() {
        let {
            uploader
        } = this;
        let result = this.getValidFiles().length + Object.keys(uploader.queue.get()).length;
        return result;
    }
    getShowType() {
        let type = 1;
        let count = Number(this.getInvalidCoverCount());
        switch (count) {
            case 0:
                type = 6;
                break;
            case 1:
            case 2:
                type = 1;
                break;
            case 3:
                type = 3;
                break;
        }
        return type.toString();
    }
    validCoverType(coverShowType) {
        let coversCount = this.getInvalidCoverCount();
        let isValidCover = true;
        switch(Number(coverShowType)) {
            case 1:
            case 2:
            case 4:
            case 5:
                isValidCover = coversCount < 1;
            break;
            case 3:
                isValidCover = coversCount < 3;
            break;
            case 6:
                isValidCover = coversCount != 0;
            break;
        }
        return !isValidCover;
    }
}

export default CoverUploader;



