const toString = Object.prototype.toString;

function onlyAssignExistKey(td, od, ignore) {
    ignore = ignore && ignore.length ? ignore : [];

    if (td && typeof td === 'object') {
        for (var key in td) {
            if (td.hasOwnProperty(key) && key in od && ignore.indexOf(key) != 0) {
                if (toString.call(od[key]) === '[object Array]') {
                    td[key] = [].concat(od[key]);
                } else if (typeof od[key] === "object") {
                    onlyAssignExistKey(td[key], od[key]);
                } else {
                    td[key] = od[key];
                }
            }
        }
    }

    return td;
}

function getUrlSearchs() {
    var search, items, item, name, value, args = {};
    search = location.search.length > 0 ? location.search.substring(1) : null;
    items = search ? search.split("&") : "";
    for (var i = 0, len = items.length; i < len; i++) {
        item = items[i].split("=");
        name = decodeURIComponent(item[0]);
        value = (item[1]|| '').replace(/(.*)%$/, (match, $1) => {
            return $1 + encodeURIComponent('%');
        });
        value = decodeURIComponent(value || '');
        if (name.length > 0) {     //防止为空的参数比如说(?page=2&=invalidParam)
            args[name] = value;
        }
    }
    return args;
}

export default {
    onlyAssignExistKey,
    getUrlSearchs
}


