var toString = Object.prototype.toString;

function AutoApplySelect() {
    this.targetCoordinate = {
        x: 0,
        y: 0,
        x2: 0,
        y2: 0
    };
}

var api = AutoApplySelect.prototype;

api.set = function(source) {
    var jcrop = source.jcrop;
    // getBounds => [w, h]
    var bounds = jcrop.getBounds();
    var select = jcrop.tellSelect();
    
    this.targetCoordinate = {
        x: select.x,
        y: select.y,
        x2: bounds[0] - select.x2,
        y2: bounds[1] - select.y2
    };
};

api.apply = function(targets) {
    var _this = this;
    var targetCoordinate = _this.targetCoordinate;
    
    targets = targets || [];
    if (toString.call(targets) !== '[object Array]') {
        targets = [targets];
    }

    targets.forEach(function(item, idx) {
        var jcrop = item.jcrop;
        var bounds = jcrop.getBounds();
        // [x, y, x2, y2]
        jcrop.setSelect([targetCoordinate.x, targetCoordinate.y, bounds[0] - targetCoordinate.x2, bounds[1] - targetCoordinate.y2]);
    });    
};


module.exports = AutoApplySelect;



