import QuRequest from '../core/QuRequest'
import q from 'q';

const quRequest = new QuRequest();
class Term {
    constructor() { }

    getTerm() {
        let rst = [];
        return quRequest.send(quRequest.Apis.qukan.sensitiveword.getListByType, { type: 1 })
        .then(({ data }) => {
            while (data.length) {
                let regData = data.splice(0, 50);
                let s = "("+ regData.join('|') +")";
                let regex = new RegExp(s, "g");
                rst.push(regex);
            }
            return rst;
        });
    }

    getVocabulary() {
        return quRequest.send(quRequest.Apis.qukan.sensitiveword.getListByType, { type: 2 })
        .then(({ data }) => {
            let s = "("+ data.join('|') +")";
            let regex = new RegExp(s, "g");
            return regex;
        });
    }
    all() {
        return q.all([this.getTerm(), this.getVocabulary()]);
    }
}

export default Term;



