import q from 'q';
import ReplaceModel from './ReplaceModel';

function getImageSize(src) {
    var IMG = new Image();
    var timeout = 5 * 1000;
    return q.Promise((resolve, reject) => {
        IMG.onload = function () {
            resolve({
                w: IMG.width,
                h: IMG.height,
                src: src
            });
        };
        IMG.onerror = function () {
            reject(new Error("获取文章中图片的尺寸失败，这会影响页面的展示，请检查一下"));
        };
        IMG.src = src;
    }).timeout(timeout);
}

function deckContent(content) {
    let imgReg = /<img[^§<>]*?[^_](src=("|').*?("|'))/gi;
    let remoteLinkReg = /http:\/\/.*?(?:inapp|qukan|static)\./;
    let srcReg = /src=(?:"|')(.*?)(?:"|')/i;
    let matchs = content.match(imgReg);
    let matchsMap = {};
    let promises = [];

    if (matchs && matchs.length) {
        matchs.forEach(src => {
            promises.push(
                q.Promise((resolve, reject) => {
                    let url = src.match(srcReg)[1];
                    // 文章中不允许使用外链图片
                    if (url && !remoteLinkReg.test(url)) {
                        throw new Error("文章中存在一些外链图片，这会导致不好的影响，请检查一下！");
                    }
                    resolve(getImageSize(url));
                }).then(function ({ w, h, src: url }) {
                    matchsMap[src.match(/src=".*"/i)[0]] = ` data-src="${url.split('?')[0]}" data-size="${w + ',' + h}" `;
                })
            );
        })

        return q.all(promises)
            .then(() => {
                for (let key in matchsMap) {
                    let value = matchsMap[key];
                    content = content.replace(key, value);
                }
                return content;
            })
    } else {
        return q.resolve(content);
    }
}

function unDeckContent(content) {
    return (content || '').replace(/(data-)(?:src|size)/gi, function (match, p1) {
        return match.replace(p1, "");
    });
}

function comparsionValue(originData, targetData, ignoreCompare) {
    var result = {};
    var oRow = null,
        tRow = null;
    var tempORow, tempTRow;
    ignoreCompare = ignoreCompare || [];

    for (var key in targetData) {
        oRow = originData[key];
        tRow = targetData[key];

        tempORow = oRow;
        tempTRow = tRow;
        typeof oRow === "object" && (tempORow = JSON.stringify(oRow));
        typeof tRow === "object" && (tempTRow = JSON.stringify(tRow));

        // 因为detail 被 "unDeckContent" 了，所以得把原始数据也 "unDeckContent" 才能正确的比对
        if (key === "detail") {
            tempORow = unDeckContent(tempORow);
        }

        if (tempORow != tempTRow || ignoreCompare.lastIndexOf(key) !== -1) {
            result[key] = tRow;
        }
    }
    return result;
}

export default {
    getImageSize,
    deckContent,
    unDeckContent,
    comparsionValue
}



