require("./webuploader/dist/webuploader.css");
var Q           = require('q');
var Queue       = require('./Queue');
var WebUploader = require("./webuploader");
let noop = function() {};

var toString = Object.prototype.toString;

/**
 * encapsulation web uploader and addtional useful api
 * implement images Queue
 */
function Uploader(option, callBacks) {
    var defaultOption = {
        auto: false,
        paste: "",
        server: "",
        fileVal: "uploadFile",
        pick: "",
        fileNumLimit: 10,
        formData: {},
        timeout: 5 * 1000,
        accept: {
            title: 'Images',
            extensions: 'gif,jpg,jpeg,bmp,png',
            mimeTypes: 'image/*'
        }
    };

    var defaultCallbacks = {
        'beforeFileQueued': noop,
        'fileQueued': noop,
        'uploadSuccess': noop,
        'uploadFinished': noop,
        'uploadError': noop
    };

    option = Object.assign(defaultOption, option); 
    callBacks = Object.assign(defaultCallbacks, callBacks);

    if (!option.server)
        throw new Error('Uploader: 请提供 server 参数');
    
    var uploaderInstance = this.uploaderInstance = WebUploader.create(option);

    var STATUS = this.STATUS = {
        pending: 0,
        uploading: 1
    };
    
    this.defer = Q.defer();
    var _this = this;
    var queue = this.queue = new Queue();
    var status = this.status = STATUS.pending;

    uploaderInstance.on('beforeFileQueued', function(file) {
        var result = true;
        if (isFunction(callBacks.beforeFileQueued)) 
            result = callBacks.beforeFileQueued(uploaderInstance); 
        
        if (!result) 
            result = queue.length < option.fileNumLimit;
        
        return result;
    })
    .on('fileQueued', function( file ) {
        isFunction(callBacks.fileQueued) && callBacks.fileQueued(uploaderInstance, file);
    })
    .on('uploadSuccess', function(file, res) {
        isFunction(callBacks.uploadSuccess) && callBacks.uploadSuccess(uploaderInstance, file, res);
        if (res.code == 0 && typeof res.data.url === "string") {
            queue.add(file.id, res.data.url);
        } else {
            file.setStatus('error', res.message);
            _this.defer.reject(new Error("上传"+ file.name +"失败"));
            console.error('Uploader：', res);
        }
    })
    .on('uploadFinished', function(file) {
        isFunction(callBacks.uploadFinished) && callBacks.uploadFinished(uploaderInstance, file); 
        _this.defer.resolve(queue.get());
    })
    .on('uploadError', function(file, reason) {
        isFunction(callBacks.uploadError) && callBacks.uploadError(uploaderInstance, file, reason);
        _this.defer.reject(new Error("上传封面失败: " + reason));
        console.error('Uploader', reason);
    });
}

var api = Uploader.prototype;

function isFunction(obj) {
    return typeof obj === "function";
}

/**
 * trigger upload images
 * @param retry {boolean} - 是否也重新上传已经失败的图片
 * @return {promise} 
 */
api.upload = function(retry) {
    var _this = this;
    var uploaderInstance = this.uploaderInstance;
    if (this.status === this.STATUS.uploading)
        return Q.reject(new Error('uploader current process is not fnished, cant\'t start upload '));

    this.defer = Q.defer();

    if (uploaderInstance.getFiles("inited").length < 1 && uploaderInstance.getFiles("error").length < 1) 
        this.defer.resolve(this.queue.get());

    this.status = this.STATUS.uploading;
    this.uploaderInstance.upload();

    if (uploaderInstance.getFiles("error").length > 0 || retry) {
        this.uploaderInstance.retry();
    }
    
    return this.defer.promise
        .catch(function(reason) {
            console.log(reason.message || reason, reason.stack);
            throw reason;
        })
        .finally(function(record) {
            _this.status = _this.STATUS.pending;
            return record;
        });
};

/**
 * padding images to queue
 * @param items {[]string|string}
 * @return void;
 */
api.padQueue = function(items) {
    if (toString.call(item) !== '[object Array]') 
        items = [items];

    items.forEach(function(item) {
        this.queue.add(item, item);
    });
};

/**
 * remove file from queue and uploaderInstance
 * @param key {string} 
 * @param file {Object} - webUploader file object
 * @return void; 
 */
api.remove = function(key, file) {
    this.queue.remove(key);
    if (file) key = file; 

    // padding 的 图片 是没有 file 对象的，移除会产生错误
    try {
        this.uploaderInstance.removeFile(key, true);
    } catch (error) { }
};

export default Uploader;



