import Uploader from '../widget/Uploader';
import q from 'q';
class ShareUploader {
    constructor({
        token,
        serverPath,
        vm,
        pick = '#filePicker',
        isEdit = false
    }) {
        this.defaultUrl = "http://inapp.1sapp.com/qukan_other/fenxian/fenxian.png"
        this.isEdit = isEdit;
        this.vm = vm;
        let shareUploader = new Uploader({
            auto: false,
            paste: "#J-image-share",
            server: serverPath,
            fileVal: "uploadFile",
            pick: '#fileShare',
            fileNumLimit: 1,
            formData: {
                token
            },
            accept: {
                title: 'Images',
                extensions: 'gif,jpg,jpeg,bmp,png',
                mimeTypes: 'image/png,image/jpg,image/jpeg,imge/bmp'
            }
        }, {
            'fileQueued': function (uploaderInstance, file) {
                uploaderInstance.makeThumb(file, function (error, src) {
                    if (error) {
                        util.showError('创建二次分享本地缩略图失败');
                        throw error;
                    }
                    vm.sharePreviewThumbs.push({
                        id: file.id,
                        name: file.name,
                        src: src
                    });
                }, 100, 100);
            }
        });

        this.uploader = shareUploader;
    }


    init(content = "") {
        let url = "";
        if (content) {
            let $div = document.createElement('div');
            $div.innerHTML = content;
            let $img = $div.querySelector('#img img');
            url = $img ? $img.src : '';
        }
        url = url || this.defaultUrl;
        this.uploader.queue.add(url, url);
        this.vm.sharePreviewThumbs.push({
            name: url,
            id: url,
            src: url
        });
    }

    isDefaultCover(src) {
        return src === this.defaultUrl;
    }

    checkAndUpload() {
        return this.uploader.upload()
            .then((record) => {
                let keys = Object.keys(record);
                if (!keys.length) {
                    throw new Error('请上传二次分享图');
                } else {
                    let src = record[keys[0]];
                    // 如果是默认图那就不检查了
                    return record;
                }
            });
    }
}

export default ShareUploader;



