var _     = require("underscore");
var $     = require("jquery");
var Event = require("./Event");
var Q     = require('q');

window.jQuery = $;
require("jquery-mousewheel");

/**
 * ScrollHelper constructor 
 * @param {object} options 
 * @param {object} config
 */
function ScrollHelper(options, config) {
    var _this = this; 
    var defaultConfig = {
        neglect: 100,
        currentIndex: 0
    };

    _this.options = options;
    config = config || {};
    config = _.assign(defaultConfig, config);
    _this.prevTimeStamp = 0;
    _this.config = config;

    var $list = options.$list;
     

    $list.on("mousewheel", function(event) {
        var width = _this.mouseWheelHandle(event);
        
        if (typeof width === 'number') {
            $list.animate({
                marginLeft: -(width) 
            }, 0);
        }

        event.preventDefault();
        event.returnValue = false;
        return false;
    });
}

var api = ScrollHelper.prototype;

api.getChild = function() {
    return this.options.$list.children().toArray();
};

api.countMoveDistance = function() {
    var childs = this.getChild();
    var distance = 0;

    for (var i = 0; i < childs.length; i++) {
        if (i < this.config.currentIndex) {
            distance += $(childs[i]).width() + 30;
        } else {
            break;
        }
    }

    return distance;
};

api.mouseWheelHandle = function mouseWheelHandle(event) {
    var _this = this;
    var config = _this.config;
    var deltaY = event.deltaY; 
    var canScroll = true;

    if (!((event.timeStamp - _this.prevTimeStamp) > config.neglect)) 
        canScroll = true;

    _this.prevTimeStamp = event.timeStamp;

    if (!canScroll) return false;
    
    var childs = _this.getChild();

    if (deltaY > 0) { // right
        if (config.currentIndex >= childs.length - 1) {
            config.currentIndex = childs.length -1
        } else {
            config.currentIndex++;
        }
    } else { // left
        if (config.currentIndex < 0) {
            config.currentIndex = 0;
        } else {
            config.currentIndex--;
        }
    }
    var width = this.countMoveDistance();
    return width;
};

module.exports = ScrollHelper;


