function Queue() {
    this.queue = {};    
    this.length = 0;
}

var api = Queue.prototype;
var toString = Object.prototype.toString;

api.add = function(key, value) {
    // 兼容数组
    if (toString.call(key) === "[object Array]" ) {
        for (var i = 0; i < key.length; i++) {
            this.length++;
            this.queue[i] = key[i];
        }
    } else {
        this.length++;
        this.queue[key] = value;    
    }
}

api.remove = function(key) {
    if (key) {
        this.length > 0 && this.length--;
        this.queue[key] = null;
        delete this.queue[key];
    } else {
        this.length = 0;
        this.queue = {};
    }
}

api.get = function(key) {
    if (key) {
        return this.queue[key];
    }

    return this.queue;
}

module.exports = Queue;

function test() {
    var q = new Queue();

    q.add('uu', 'nei hao');
    q.add(['yoyo', 'haha', 'didi']);
    console.log(q.get());
    q.remove('uu');
    console.log(q.get());
    q.remove();
    console.log(q.get());
}


