<template>
    <div class="">
        <el-form ref="form"
                 :model="form"
                 :rules="validRules"
                 class="form-inline"
                 label-width="100px"
                 v-loading="loading"
                 element-loading-text="拼命加载中">
            <el-row :gutter="20">
                <el-col :lg="14">
                    <div class="ibox">
                        <div class="ibox-title"><h3>基本信息</h3></div>
                        <div class="ibox-content">
                            <el-form-item prop="title" label="标题" class="form-item-title">
                                <el-input @blur="handleFormatTitle($event)" placeholder="请输入文章标题" v-model.trim="form.title">
                                    <template slot="append">
                                        <div class="title-counter">{{ form.title.length || 0 }} / 26</div>
                                    </template>
                                </el-input>
                                <el-tag @click.native="handleFormatTitle($event, true)" class="el-icon-setting"> 格式化</el-tag>
                                <el-tag @click.native="handleCheckRepeatTitle($event)" class="el-icon-more"> 验重</el-tag>
                            </el-form-item>
                            <el-form-item prop="type" label="分类">
                                <el-select v-model="form.type">
                                    <el-option v-for="(item, $key) in Types.articleTypesMap" :label="item" :value="$key"></el-option>
                                </el-select>
                            </el-form-item>
                            <!--<el-form-item label="简介">
                                <el-input type="textarea" v-model="form.introduction"></el-input>
                            </el-form-item>-->
                            <el-form-item label="来源类型">
                                <el-input v-model="form.source" disabled></el-input>
                            </el-form-item>
                            <el-form-item label="来源名称" prop="source_name">
                                <el-row :gutter="20">
                                    <el-col :lg="15">
                                        <el-input v-model.trim="form.source_name"></el-input>
                                    </el-col>
                                    <el-col :lg="5">
                                        <el-tooltip content="是否显示来源名称" placement="top">
                                            <el-switch
                                                    v-model="form.extend.hide_source_name"
                                                    on-color="#13ce66"
                                                    off-color="#ff4949"
                                                    on-value="0"
                                                    off-value="1">
                                            </el-switch>
                                        </el-tooltip>
                                    </el-col>
                                </el-row>
                            </el-form-item>
                            <el-form-item label="来源url" prop="source_url">
                                <el-input v-model.trim="form.source_url" :disabled="form.show_type == 1"></el-input>
                            </el-form-item>
                            <el-form-item label="来源发布时间" prop="source_time">
                                <el-input v-model.trim="form.source_time" :disabled="form.show_type == 1" @blur="formatOriginPublishTime($event)"></el-input>
                            </el-form-item>
                        </div>
                    </div>

                </el-col>
                <el-col :lg="10">
                    <div class="ibox">
                        <div class="ibox-title"><h3>指定信息</h3></div>
                        <div class="ibox-content">
                            <el-form-item label="指定浏览地址">
                                <el-input v-model="form.assign_url" placeholder="如果指定了浏览地址，此文章将会跳转到该地址"></el-input>
                                <p>
                                    <a :href="form.assign_url" target="_blank" v-text="form.assign_url" rel="noreferrer"></a>
                                    <small v-show="form.assign_url.length > 0" class="text-warning">[请检查链接是否可以正常访问]</small>
                                </p>
                            </el-form-item>
                            <el-form-item label="指定区域">
                                <el-select v-model="form.target_location">
                                    <el-option value="" label="不指定"></el-option>
                                    <el-option v-for="item in Types.province" :value="item" :label="item"></el-option>
                                </el-select>
                            </el-form-item>
                        </div>
                    </div>
                </el-col>

                <el-col :lg="24">
                    <div class="ibox">
                        <div class="ibox-title"><h3>文章内容</h3></div>
                        <div class="ibox-content">
                            <el-form-item label="封面类型">
                                <el-radio-group v-model="form.cover_show_type">
                                    <el-radio label="1">左图</el-radio>
                                    <el-radio label="2">右图</el-radio>
                                    <el-radio label="3">三图</el-radio>
                                    <el-radio label="4">大图</el-radio>
                                    <el-radio label="5">长图</el-radio>
                                    <el-radio label="6">无图</el-radio>
                                </el-radio-group>
                            </el-form-item>
                            <el-form-item label="文章封面">
                                <div id="J-image-list" class="cover-wrap">
                                    <div class="file-picker-head"><span>最多可上传3张，显示类型为单图或大图时，取第一张。</span></div>
                                    <ul class="clearfix">
                                        <li v-for="item in coverPreviewThumbs" class="file-item thumbnail">
                                            <a :href="item.src" target="_blank"><img :src="item.src | thumbSrcSize"></a>
                                            <div class="info" v-text="item.name"></div>
                                            <i @click="removeThumbAction($event, item)" class="close">×</i>
                                        </li>
                                    </ul>
                                    <div class="btn-wrap">
                                        <div id="filePicker" class="pull-left">选择图片</div>
                                    </div>
                                </div>
                            </el-form-item>
                            <el-form-item label="二次分享图">
                                <el-button type="primary" size="mini" @click="showShareCover = !showShareCover">显示/隐藏</el-button>
                                <div v-show="showShareCover" id="J-image-share" class="cover-wrap">
                                    <div class="file-picker-head"><span>图片尺寸需大于400*400</span></div>
                                    <ul class="clearfix">
                                        <li v-for="item in sharePreviewThumbs" class="file-item thumbnail">
                                            <a :href="item.src" target="_blank"><img :src="item.src | thumbSrcSize"></a>
                                            <div class="info" v-text="item.name"></div>
                                            <i @click="removeShareAction($event, item)" class="close">×</i>
                                        </li>
                                    </ul>
                                    <div class="btn-wrap">
                                        <div id="fileShare" class="pull-left">选择图片</div>
                                    </div>
                                </div>
                            </el-form-item>
                            <el-form-item label="工具">
                                <el-button @click="handleDispose($event)">图片裁剪</el-button>
                                <el-button @click="handleGrabPhoto($event)">转换外链图片</el-button>
                                <el-button @click="handleReloadPhoto($event)">重载图片</el-button>
                                <el-button @click="handleArtRepeat($event)">验重</el-button>
                            </el-form-item>
                            <el-form-item label=" ">
                                <el-button @click="handleKeyExcute($event)" size="small" type="info">一键执行</el-button>
                                <el-button @click="handleReplaceHandle($event, 'removeAllEmptyLine')" size="small" type="info">删除所有空行</el-button>
                                <el-button @click="handleReplaceHandle($event, 'addText')" size="small" type="info">添加文字</el-button>
                                <el-button @click="handleReplaceHandle($event, 'clearStyle')" size="small" type="info">清除字体和字体大小</el-button>
                                <el-button @click="handleReplaceHandle($event, 'clearBR')" size="small" type="info">清除br</el-button>
                                <el-button @click="handleReplaceHandle($event, 'imgStyle')" size="small" type="info">清除图片样式</el-button>
                                <el-button @click="handleReplaceHandle($event, 'clearWordSpace')" size="small" type="info">清除字与字之间的空格</el-button>
                                <el-button @click="handleReplaceHandle($event, 'canvSmy')" size="small" type="info">半角转全角</el-button>
                                <el-button @click="handleReplaceHandle($event, 'removeTable')" size="small" type="info">标签替换</el-button>
                                <el-button @click="handleReplaceHandle($event, 'term')" size="small" type="info">标准化数据屏蔽</el-button>
                                <el-button @click="handleReplaceHandle($event, 'endSymbol')" size="small" type="info">段落结束句号添加</el-button>
                                <el-button @click="handleReplaceHandle($event, 'correct')" size="small" type="info">引号纠正</el-button>
                                <el-button @click="handleImageBreak($event)" size="small" type="info">图片换行</el-button>
                            </el-form-item>
                            <el-form-item label="内容">
                                <VueUEditor style="line-height: 1;" @ready="editReady" ueditorPath="/assets-root/ueditor/" :ueditorConfig="artUEConfig"></VueUEditor>
                            </el-form-item>
                            <el-form-item label="标签">
                                <tagsinput
                                        :tags="form.tag"
                                        @tags-change="handleTagChange"></tagsinput>
                                <el-button v-for="tag in rmdTagList" @click="handleAddTag(tag)" type="primary" size="mini">{{tag}}</el-button>
                            </el-form-item>
                        </div>
                    </div>
                </el-col>

                <el-col :lg="12">
                    <div class="ibox">
                        <div class="ibox-title">
                            <h3>其他</h3>
                        </div>
                        <div class="ibox-content">
                            <el-form-item label="文章质量">
                                <el-radio-group size="small" class="radioButton" v-model="form.extend.quality">
                                    <el-radio-button
                                            v-for="item in Types.qualityStatus"
                                            :label="item.key"
                                            :key="item.key">
                                        {{item.name}}
                                    </el-radio-button>
                                </el-radio-group>
                            </el-form-item>
                            <el-form-item label="文章时效">
                                <el-radio-group size="small" class="radioButton" v-model="form.extend.life">
                                    <el-radio-button
                                            v-for="item in Types.lifeStatus"
                                            :label="item.key"
                                            :key="item.key">
                                        {{item.name}}
                                    </el-radio-button>
                                </el-radio-group>
                            </el-form-item>
                            <el-form-item label="默认分享数">
                                <el-input v-model="form.share_count"></el-input>
                            </el-form-item>
                            <el-form-item label="默认浏览数">
                                <el-input v-model="form.read_count"></el-input>
                            </el-form-item>
                            <el-form-item label="显示标示">
                                <el-select v-model="form.extend.tips">
                                    <el-option value="" label="默认"></el-option>
                                    <el-option v-for="item in Types.mark" :value="item" :label="item"></el-option>
                                </el-select>
                            </el-form-item>
                        </div>
                    </div>
                </el-col>
                <el-col :lg="12">
                    <div class="ibox">
                        <div class="ibox-title">
                            <h3>控制属性
                                <small>&nbsp;&nbsp;（OFF：关闭；ON：开启）</small>
                            </h3>
                        </div>
                        <div class="ibox-content">
                            <el-row :gutter="20">
                                <el-col :span="12">
                                    <el-form-item label="不感兴趣按钮">
                                        <el-switch v-model="form.extend.disable_unlike" on-color="#13ce66" off-color="#ff4949" on-value="0" off-value="1"></el-switch>
                                    </el-form-item>
                                    <el-form-item label="开启打赏">
                                        <el-switch v-model="form.extend.can_reward_coin" on-color="#13ce66" off-color="#ff4949" on-value="1" off-value="0"></el-switch>
                                    </el-form-item>
                                    <el-form-item label="关闭广告">
                                        <el-switch v-model="form.extend.skip_ad" on-color="#13ce66" off-color="#ff4949" on-value="1" off-value="0"></el-switch>
                                    </el-form-item>
                                    <el-form-item label="可以评论">
                                        <el-switch v-model="form.can_comment" on-color="#13ce66" off-color="#ff4949" on-value="1" off-value="2"></el-switch>
                                    </el-form-item>
                                    <el-form-item label="强制H5">
                                        <el-switch v-model="form.extend.native_off" on-color="#13ce66" off-color="#ff4949" on-value="1" off-value="0"></el-switch>
                                    </el-form-item>

                                </el-col>
                                <el-col :span="12">
                                    <el-form-item label="隐藏评论">
                                        <el-switch v-model="form.hide_comment" on-color="#13ce66" off-color="#ff4949" on-value="2" off-value="1"></el-switch>
                                    </el-form-item>
                                    <el-form-item label="分享">
                                        <el-switch v-model="form.share_type" on-color="#13ce66" off-color="#ff4949" on-value="0" off-value="3"></el-switch>
                                    </el-form-item>
                                    <!--<el-form-item label="默认展开全文">-->
                                        <!--<el-switch v-model="form.ret" on-color="#13ce66" off-color="#ff4949" on-value="1" off-value="0"></el-switch>-->
                                    <!--</el-form-item>-->
                                    <el-form-item label="负面新闻">
                                        <el-switch v-model="form.extend.negative" on-color="#13ce66" off-color="#ff4949" on-value="1" off-value="0"></el-switch>
                                    </el-form-item>
                                    <el-form-item label="是否展开全文">
                                        <el-switch v-model="form.extend.rc" on-color="#13ce66" off-color="#ff4949" on-value="1" off-value="0"></el-switch>
                                    </el-form-item>
                                </el-col>
                            </el-row>
                        </div>
                    </div>
                </el-col>

                <el-col :lg="24">
                    <div class="ibox">
                        <div class="ibox-title"><h3>提交</h3></div>
                        <div class="ibox-content">
                            <el-form-item label="延迟发布时间">
                                <el-date-picker
                                        v-model="form.publish_time"
                                        type="datetime"
                                        placeholder="选择延迟发布时间">
                                </el-date-picker>
                            </el-form-item>
                            <el-form-item label="著作权?">
                                <el-switch v-model="hasCopyright" on-color="#13ce66" off-color="#ff4949" on-value="1" off-value="2" on-text="有" off-text="没有" ></el-switch>
                            </el-form-item>
                            <el-form-item label="是手发的文章吗？">
                                <el-switch v-model="form.extend.inner" on-color="#13ce66" off-color="#ff4949" on-value="1" off-value="2" on-text="是" off-text="不是" ></el-switch>
                            </el-form-item>
                            <el-form-item>
                                <el-button :disabled="this.loading" type="info" @click="handleSubmit($event)">提交</el-button>
                                <el-button :disabled="this.loading" type="primary" @click="handleSubmit($event, true)">提交并发布</el-button>
                            </el-form-item>
                        </div>
                    </div>
                </el-col>
            </el-row>
        </el-form>
    </div>
</template>

<script>
//    require("alertifyjs/build/css/themes/default.css");
//    require("alertifyjs/build/css/alertify.css");
//    import alertify from 'alertifyjs';
    import q from 'q';
    import $ from 'jquery';
    import myMoment from '../../module/MyMoment';
    import moment from 'moment';
//    import deepcopy from 'deepcopy' ;
    import tagsinput from 'vue-tagsinput';
    import auditConfig from '../../module/AuditConfig';
    import contentConfig from '../../module/ContentConfig';
    import CoverUploader from '../../module/CoverUploader';
    import ShareUploader from '../../module/ShareUploader';
    import ContentHelp from '../../module/ContentHelp';
    import help from '../../module/help';
    import VueUEditor from 'vue-ueditor';
    import ReplaceModel from '../../module/ReplaceModel'
    import Term from './../../module/Term';
    import SplitWord from '../../module/SplitWord';
    import artUEConfig from '../../assets/art-ue-config';
    import GrabPhoto from '../../module/GrabPhoto';
    import PhotoDispose from '../../widget/PhotoDispose';
    import QuRequest from '../../core/QuRequest';
    import ContentSimilar from '../../module/Similar/ContentSimilar';
    import Auth from '../../core/Auth'

    const quRequest = new QuRequest();

    let contentSimilar = new ContentSimilar();
    let grabPhoto = new GrabPhoto();
    let coverUploader = null;
    let shareUploader = null;
    let ue = null;
    let originData = null;
    let screeningData = {
        term: null,
        vocabulary: null
    };
    let photoDispose = null;


    export default {
        components: {
            tagsinput,
            VueUEditor
        },
        beforeRouteLeave(to, from, next) {
            photoDispose.distory();
            next(true);
        },
        mounted() {
            let { $route, $message } = this;
//            let token = this.$store.state.app.token;
            let token = Auth.getToken();
            let contentId = this.form.id = $route.query.id;
            // 新建文章时，分类默认为列表页筛选的分类
            !contentId && $route.query.type && (this.form.type = $route.query.type);

            this.getCategorys();
            photoDispose = new PhotoDispose(document.body, this.$message);
            photoDispose.on('uploaded', (record) => {
                record.success.forEach((item, idx) => {
                    item.jcrop.destroy();
                    item.$previewCover.closest('li').remove();
                    item.originImg.src = item.url + "?imageView2/1/q/70";
                    item.originImg.setAttribute('_src', item.url);
                });

                if (!record.error.length) {
                    photoDispose.close(true);
                }

                this.$message(`成功处理 ${ record.success.length } 张图片, 失败: ${ record.error.length } 张`);
            });

            coverUploader = new CoverUploader({
                token,
                serverPath: quRequest.getUrl(quRequest.Apis.qukan.upload.uploadFile),
                vm: this,
                pick: '#filePicker',
                isEdit: !!contentId
            });
            shareUploader = new ShareUploader({
                token,
                serverPath: quRequest.getUrl(quRequest.Apis.qukan.upload.uploadFile),
                vm: this,
                pick: '#fileShare',
                isEdit: !!contentId
            });

            new Term()
                .getTerm()
                .then(term => {
                    screeningData.term = term;
                    if (contentId) {
                        return this.getContent($route.query.id)
                            .then(() => {
                                let { cover: covers, detail } = this.form;
                                if (covers && covers.length > 0) {
                                    covers.forEach((url) => {
                                        this.coverPreviewThumbs.push({
                                            name: url,
                                            id: url,
                                            src: url
                                        });
                                    });
                                }
                                coverUploader.init();
                                shareUploader.init(detail);
                            });
                    } else {
                        shareUploader.init();
                    }
                }).catch(reason => {
                $message.error(reason.message);
            }).fin(() => {
                this.loading = false;
            });
        },
        data() {
            return {
                hasCopyright: "2",
                loading: true,
                artUEConfig: artUEConfig,
                showShareCover: false,
                form: {
                    "id": '',
                    "content_type": 1,
                    "source": "",
                    "source_time": "",
                    "source_url": "",
                    "source_type": "",
                    "source_name": "",
                    "title": "",
                    "cover": "",
                    "cover_show_type": "6",
                    "image_list": "",
                    "type": "",
                    "introduction": "",
                    "detail": "",
                    "media_num": "",
                    "share_count": "0",
                    "read_count": "0",
                    "ret": "0",
                    "assign_url": "",
                    "is_delay": "",
                    "publish_time": "",
                    "show_type": "2",
                    "can_comment": "1",
                    "hide_comment": "1",
                    "share_type": "0",
                    "tag": [],
                    "show_price": "2",
                    "target_location": "",
                    "extend": {
                        "src_title": '',    // 采集时的源站标题
                        "hide_source_name": "0",  // 是否显示来源名称
                        "disable_unlike": "0",    // 是否禁用不感兴趣
                        "font_color": '',   // 标题颜色
                        "font_bold": "0",     // 标题加粗
                        "can_reward_coin": "0",   // 是否开启打赏
                        "skip_ad": "0",   // 是否关闭广告
                        "negative": "0",  // 负面信息
                        "tips": '', // 文章标示
                        "quality": "2",   // 文章质量 1(降权) | 2（常规） | 3（加权
                        "life": "1",   // 文章时效 1（无时效）| 2（短时效）| 3（长时效）,
                        "inner": "2",    // 标记文章是否属于编辑手动发布,
                        // 是否强制使用h5页面， 0 （默认）1（强制）
                        "native_off": "0",
                        "rc": "0"  // 是否展开全文。0：不展开，1：展开。
                    }
                },
                Types: {
                    articleTypes: [],
                    articleTypesMap: {},
                    qualityStatus: auditConfig.qualityStatus,
//                    lifeStatus: auditConfig.lifeStatus,
                    province: contentConfig.province,
                    mark: contentConfig.mark
                },
                coverPreviewThumbs: [],
                sharePreviewThumbs: [],
                validRules: {
                    "source_name": [
                        { required: true, message: '请填写来源名称', trigger: 'blur' }
                    ],
                    "source_url": [
                        { required: true, message: '请填写来源URL', trigger: 'blur' }
                    ],
                    "source_time": [
                        { required: true, message: '请填写来源发布时间', trigger: 'blur' }
                    ],
                    "type": [
                        { required: true, message: '请填写分类' }
                    ],
                    "title": [
                        { required: true, message: '请填写标题' }
                    ]
                },
                rmdTagList: []
            }
        },
        filters: {
            thumbSrcSize(src) {
                if (/^http/.test(src)) {
                    if (src.lastIndexOf('?') !== -1) {
                        return src.replace(/\?/, '?imageView2/1/w/100/h/100/q/50&');
                    } if(src.lastIndexOf('#') !== -1) {
                        return src.replace(/\#/, '?imageView2/1/w/100/h/100/q/50#');
                    } else {
                        return src + '?imageView2/1/w/100/h/100/q/50&';
                    }
                }
                return src;
            }
        },
        watch: {
            'hasCopyright'(nv, ov) {
                ['source_name', 'source_url', 'source_time'].forEach((name, idx) => {
                    this.validRules[name][0].required = nv !== "1";
                })
            }
        },
        methods: {
            handleReplaceHandle($event, action) {
                let html = ue.getContent();
                switch(action) {
                    case 'clearStyle':
                    case 'clearBR':
                    case 'imgStyle':
                    case 'clearWordSpace':
                    case 'canvSmy':
                    case 'correct':
                    case 'removeTable':
                        html = ReplaceModel[action](html);
                        break;
                    case 'removeAllEmptyLine':
                        html = ReplaceModel.removeAllEmptyLine(html, window.UE);
                        break;
                    case 'addText':
                        html = ReplaceModel.addText(html);
                        html = ReplaceModel.removeAllEmptyLine(html, window.UE);
                        break;
                    case 'term':
                        html = ReplaceModel.term(html, screeningData.term);
                        html = ReplaceModel.removeAllEmptyLine(html, window.UE);
                        break;
                    case 'endSymbol':
                        let div = $('<div>').html(html);
                        let end = ['，','。','！','？','）','：','、','>'];
                        div.find('p').each(function(){
                            if($.inArray($(this).html().substr(-1), end) === -1){
                                $(this).html($(this).html()+'。');
                            }
                        });
                        html = div.html();
                        break;
                }
                ue.setContent(html);
            },
            handleFormatTitle($event, processSpace) {
                let title = this.form.title;
                title = ReplaceModel.replaceSymbol(title);
                if (processSpace) {
                    title = ReplaceModel.replaceSpace(title);
                }
                this.form.title = title;
            },
            handleCheckRepeatTitle($event) {
                this.checkSimilar(this.form.title, this.form.id, 1)
                    .done();
            },
            handleDispose($event) {
                let $elem = $event.target;
                let imgs = [];
                let images = [].slice.call(ue.document.images);

                images.forEach(elem => {
                    let src = elem.src;
                    if (!/\.gif$/.test(src)) {
                        imgs.push({
                            elem: elem,
                            src: src
                        });
                    }
                });

                $elem.setAttribute('disabled', true);
                $elem.innerText = '准备中';

                photoDispose.fill(imgs, true);
                photoDispose.once('ready_check', function() {
                    $elem.removeAttribute('disabled');
                    $elem.innerText = '图片裁剪';
                });
            },
            handleKeyExcute() {
                ReplaceModel.keyExecution(ue.getContent(), window.UE)
                    .then((html) => {
                        ue.setContent(html);
                    })
            },
            handleImageBreak() {
                var domUtils = UE.dom.domUtils;
                var $imgs = ue.body.getElementsByTagName("img");
                $imgs = [].slice.call($imgs);

                $imgs.forEach(function($img, index) {

                    var $parent = $img.parentElement;
                    var count = domUtils.getChildCount($parent);

                    if (count > 1) {
                        var p = document.createElement("p");
                        p.appendChild($img.cloneNode());
                        $parent.replaceChild(p, $img);
                        domUtils.breakParent( p, $parent);
                    }

                });
            },
            handleGrabPhoto($event) {
                if (this.loading) return;
                this.loading = true;
                grabPhoto.grab(ue.document.images)
                    .then((values) => {
                        this.$message.success(`成功处理外链图片${ values.length }个`);
                        ue.setContent(ue.body.innerHTML);
                    })
                    .catch(reason => {
                        this.$message.error(reason.message);
                        throw reason;
                    })
                    .fin(() => {
                        this.loading = false;
                    }).done();
            },
            handleReloadPhoto() {
                let imgs = [].slice.call(ue.document.images);
                imgs.forEach(elem => {
                    let src = elem.src;
                    elem.src += src.indexOf('?') !== -1 ? '&t=' + Date.now() : '?t=' + Date.now();
                })
            },
            handleArtRepeat($event) {
                this.loading = true;
                this.checkSimilar(ue.getPlainTxt(), this.form.id)
                    .fin(() => {
                        this.loading = false;
                    });
            },
            editReady(instance) {
                ue = instance;
                this.setContent();
            },
            removeThumbAction($event, item) {
                let uploader = coverUploader.uploader;
                if (uploader.status == uploader.STATUS.uploading)
                    return false;
                uploader.remove(item.id);
                let idx = this.coverPreviewThumbs.indexOf(item);
                this.coverPreviewThumbs.splice(idx, 1);
                this.form.cover_show_type = coverUploader.getShowType();
            },
            removeShareAction($event, item) {
                let uploader = shareUploader.uploader;
                if (uploader.status == uploader.STATUS.uploading)
                    return false;
                uploader.remove(item.id);
                let idx = this.sharePreviewThumbs.indexOf(item);
                this.sharePreviewThumbs.splice(idx, 1);
            },
            formatOriginPublishTime: function($event) {
                let time = this.form.source_time;
                if(time.split(' ')[0].length < 7){
                    let year = new Date().getFullYear();
                    time = year + '-' + time;
                }
                time = time.replace(/[年月]/g,'-');
                time = time.replace(/[日]/g,'');
                this.form.source_time = time;
            },
            handleSubmit($event, needPublish) {
                let { $message, form, $refs, $router } = this;
                let $elem = $event.target;

                return q.fcall(() => {
                    if (!coverUploader.validCoverType(this.form.cover_show_type)) {
                        throw new Error('封面显示类型与图片数量不符合，请修改');
                    }
                })
                    .then(() => {
                        return q.Promise((resolve, reject) => {
                            $refs.form.validate(valid => {
                                resolve(valid);
                            });
                        })
                    })
                    .then(valid => {
                        if (!valid) {
                            throw new Error("表单验证未通过, 填写数据有误请检查");
                        }
                    })
                    .then(() => {
                        return this.checkSimilar(ue.getPlainTxt(), this.form.id);
                    })
                    .then(repeat => {
                        if (repeat) {
                            return q.Promise((resolve, reject) => {
                                alertify.confirm("是否继续发布？",
                                    () => {
                                        resolve();
                                    },
                                    () => {
                                        reject(new Error("已取消发布文章"));
                                    });
                            });
                        }
                    })
                    .then(() => {
                        this.loading = true;
                        let formData = Object.assign({},this.$data.form);
                        formData.extend = JSON.stringify(formData['extend']);
                        formData.is_delay = formData.publish_time ? 1 : 2;
                        formData.publish_time = moment(formData.publish_time).format('YYYY/MM/DD HH:mm:ss');
                        formData.tag = formData.tag.join(',');

                        return formData;
                    })
                    .then(formData => {
                        let $uediv = $('<div>');
                        $uediv.append(ue.getContent());
                        $uediv.find('.active').each(function(){
                            $(this).replaceWith($(this).html());
                        });
                        $uediv.html( ReplaceModel.removeAllEmptyLine($uediv.html(), window.UE) );
                        formData.detail = $uediv.html();
                        //修改时只提交有改动的数据
                        if (this.form.id) {
                            formData = ContentHelp.comparsionValue(originData, formData, ['id', 'is_delay', 'detail']);
                        }
                        return formData;
                    })
                    .then(formData => {
                        return ContentHelp.deckContent(formData.detail)
                            .then(content => {
                                formData.detail = content;
                                return formData;
                            });
                    })
                    .then(formData => {
                        return q.all([coverUploader.uploader.upload() ,shareUploader.checkAndUpload()])
                            .then(([artCover, shareCover]) => {
                                let covers = [];
                                let share = [];
                                // 封面只需要保存 path
                                this.coverPreviewThumbs.forEach(function(thumb, idx) {
                                    covers.push(artCover[thumb.id].replace(/^(http[s]{0,1}:\/\/.*?\/)/, function(match, $1) {
                                        return match.replace($1, "");
                                    }));
                                });
                                this.sharePreviewThumbs.forEach(function(thumb, idx) {
                                    share.push(shareCover[thumb.id]);
                                });

                                // 如果没有自定义封面图, 将选择封面图的第一张作为封面图
                                let firstCover = artCover[Object.keys(artCover)[0]];
                                if (shareUploader.isDefaultCover(share[0]) && firstCover) {
                                    share[0] = firstCover;
                                    share[0] = share[0].split('?')[0];
                                }
                                return {
                                    artCovers: covers,
                                    shareCoverUrl: share[0],
                                    formData: formData
                                };
                            });
                    })
                    .then(({artCovers, shareCoverUrl, formData}) => {
                        formData.cover = JSON.stringify(artCovers);
                        formData.detail = `<p id="img" style="display:none"><img src="${shareCoverUrl}?imageView2/5/w/400/h/400" alt="" /></p>` + formData.detail;
                        return formData;
                    })
                    .then(formData => {
                        let api = this.form.id ? quRequest.Apis.qukan.content.modifyContent : quRequest.Apis.qukan.content.addContent;
                        return quRequest.send(api, formData)
                            .then(({ data }) => {
                                $message.success("保存内容成功");
                                return data;
                            });
                    })
                    .then(responseData => {
                        if (needPublish) {
                            $message("正在修改为发布状态");
                            return quRequest.send(quRequest.Apis.qukan.content.modifyContentStatus, {
                                id: responseData.id,
                                status: "2",
                                show_type: responseData.show_type,
                            })
                                .then(function(res) {
                                    $message.success("修改状态成功");
                                })
                        }
                    })
                    .then(() => {
                        setTimeout(function() {
                            $router.replace({ name: 'PushContentManage' })
                        }, 200);
                    })
                    .catch(reason => {
                        $message.error(`提交文章发生错误：${ reason.message || '未知错误' }`);
                        throw reason;
                    })
                    .fin(() => {
                        this.loading = false;
                    }).done();
            },
            handleTagChange(idx, text) {
                if (text) {
                    if (this.form.tag.length < 6) {
                        this.form.tag.splice(idx, 0, text)
                    }
                } else {
                    this.form.tag.splice(idx, 1)
                }
            },
            handleAddTag(tag) {
                if (this.form.tag.indexOf(tag) === -1 && this.form.tag.length < 6) {
                    this.form.tag.push(tag);
                }
            },
            getCategorys() {
                quRequest.send(quRequest.Apis.qukan.content.getDefaultChannelList)
                    .then(res => {
                        let data = res.data;
                        if (res.code == 0) {
                            this.Types.articleTypesMap = {};
                            data.forEach((type) => {
                                this.Types.articleTypesMap[type.id] = type.name;
                            });
                            this.articleTypes = data;
                        } else {
                            throw new Error(data.message || '未知错误')
                        }
                    }).done();
            },
            getContent(id) {
                this.loading = true;
                let { api, $message, $refs } = this;

                return quRequest.send(quRequest.Apis.qukan.content.getAdminContentDetail, { id })
                    .then(({ data }) => {
                        let { extend, cover: covers, tag, publish_time: publishTime } = data;

                        originData = Object.assign({},data);
                        data["detail"] = ContentHelp.unDeckContent(data["detail"]);

                        try {
                            extend = JSON.parse(extend);
                            if (!Object.keys(extend).length) {
                                throw new Error('extend 字段无效');
                            }
                            data["extend"] = extend;
                        } catch (error) {
                            $message.error('extend 字段解析失败' + extend);
                            delete data["extend"];
                        }
                        data["publish_time"] = publishTime && publishTime != 0 ? myMoment(publishTime).toFormat() : "";
                        data['tag'] = tag ? tag : [];
                        // 服务端会错误返回 share_type: -1; 这里做一下兼容 0: 开启, 3: 关闭
                        data['share_type'] = data['share_type'] != 3 ? '0' : '3';
                        help.onlyAssignExistKey(this.form, data);
                        this.setContent();
                    }).catch(reason => {
                        throw new Error(`初始化获取内容失败：${reason.message}`);
                    })
            },
            setContent() {
                let detail = this.form.detail;
                if (detail && ue) {
                    detail = ReplaceModel.term(detail, screeningData.term);
                    detail = ReplaceModel.removeAllEmptyLine(detail, window.UE);
                    let $detail = $('<div></div>').html(detail);
                    $detail.find('#img').remove();
                    ue.setContent($detail.html());
                    this.checkSimilar(ue.getPlainTxt(), this.form.id);
                    this.getSplitWord(ue.getPlainTxt(), 15);
                }
            },
            checkSimilar(content = "", id, check_type) {
                return contentSimilar.check({
                    content,
                    contentId: id,
                    check_type
                }).then(record => {
                    let similarTypeAsName = check_type === 1 ? '标题' : '内容';
                    if (record) {
                        let { docUrls, docIds } = record;
                        let h = this.$createElement;
                        let list = [];
                        docUrls.forEach((doc, idx) => {
                            list.push(h(
                                'a',
                                {
                                    attrs: {
                                        'target': '_blank',
                                        'href': doc.url
                                    }
                                },
                                `${ docIds[idx] + ' ' + doc.title } `));
                        });
                        this.$notify({
                            title: `这里有一些相似${ similarTypeAsName }`,
                            message: h('div', { 'class': 'notify-similar'}, list),
                            duration: 0
                        });
                    } else {
                        this.$message(`没有检测到相似的${ similarTypeAsName }`);
                        return false;
                    }
                }).catch(reason => {
                    this.$message.error(`验重失败：${ reason.message }`);
                    throw reason;
                });
            },
            getSplitWord(content, num) {
                return SplitWord(content, num)
                    .then((keywords = []) => {
                        let obj = {};
                        let values = [];
                        keywords.forEach((key) => {
                            if (!(key in obj)) {
                                obj[key] = null;
                                values.push(key);
                            }
                        });
                        this.rmdTagList = values;
                    }).catch(reason => {
                        this.$message.error(`获取分词数据失败: ${ reason.message || '' }`)
                    });
            }
        }
    }
</script>

<style lang="scss" type="text/scss">
    @import '../../assets/css/contentEdit.less';
    .form-item-title {
        .el-tag {
            cursor: default;
        }
        .title-counter {
            min-width: 50px;
            text-align: right;
        }
    }
    .edit-action{
        margin-left:100px;
    }
</style>



