import q from 'q';
import BaseRequest from '../../core/BaseRequest';
import Apis from '../../core/Apis'
import ApiDomain from '../../core/ApiDomain'
import Auth from '../../core/Auth'

class ContentSimilar {
    constructor() {
        this._request = new BaseRequest();
        this._apiDomain = new ApiDomain();
    }

    check({
        content,
        content_type = 1, // 1/2/3 去重的范围 文章/图片/视频 默认为1
        check_type = 0, // 文本为正文时，值为0， 文本为标题时，值为1. 默认为0
        similar_threshold = 0.8,
        contentId
    }) {
        let { _request, _apiDomain } = this;
        let api = Apis.qukan.content.viewSimilarity;
        return _request.send({
            url: _apiDomain.getUrl(api),
            type: api.type,
            timeout: 10 * 10000,
            headers: api.headers
        }, { content, content_type, check_type, threshold: similar_threshold, id: contentId, token: Auth.getToken() })
        .then(response => {
            let res = response.data;
            let data = res.data;
            let docIds = [];
            let docUrls = [];

            if (res.code != 0) {
                throw new Error('检查相似度失败： ' + res.message);
            }
            if (data.doc_url && data.doc_url.length) {
                data.doc_id.forEach((id, idx) => {
                    if (!contentId || id != contentId) {
                        docIds.push(id);
                        docUrls.push(data.doc_url[idx]);
                    }
                });
                if (docIds.length) {
                    return {
                        docUrls,
                        docIds
                    }
                } else {
                    return false;
                }
            } else {
                return false;
            }
        })
    }
}

export default ContentSimilar;



