import q from 'q';
import axios from 'axios';
// import { getUrl, request } from './Api';
import QuRequest from '../core/QuRequest';
import auth from '../core/Auth';

const quRequest = new QuRequest();

class GrabPhoto {
    constructor(failClass = 'ue_grab-img-fail') {
        let promises = [];
        this.outlinkReg = /^http[s]{0,1}:\/\/(?:static\.1sapp.com|test-static.qukan.aimodou.net)/;
        this._failClass = failClass;
    }

    excute($img) {
        let src = $img.src;
        return quRequest.send(quRequest.Apis.qukan.collect.grabImage, {
            imageUrl: src
        }).then( ({ data }) => {
            if (data.image) {
                $img.src = data.image;
                $img._src = data.image;
                $img.className = $img.className.replace(new RegExp(this._failClass), '');
                return data;
            } else {
                return new Error(data.message);
            }
        }).catch(err => {
            $img.className = $img.className + ' ' + this._failClass;
            return new Error(err.message);
        });
    }

    grab($imgs = []) {
        let errorCount = 0;
        let promises = [];
        return q.resolve()
        .then(() => {
            $imgs = [].slice.call($imgs);
            $imgs.forEach(($img, idx) => {
                let src = $img.src;
                if (src && !(this.outlinkReg.test(src))) {
                    promises.push(this.excute($img));
                }
            });
        })
        .then(() => {
            return q.all(promises);
        })
        .then((values = []) => {
            let errorCount = 0;
            values.forEach(v => {
                if (v instanceof Error) {
                    errorCount++;
                }
            });

            if (errorCount) {
                throw new Error(`批量处理外链图片失败${ errorCount }个（请注意红边）`);
            }
            return values;
        });
    }
}

export default GrabPhoto;



