import auth from "../../core/Auth";
import QuRequest from '../../core/QuRequest'
require("./static/style.less");
require("./static/jquery.Jcrop.css");
var $ = require("jquery");
window.jQuery = $;
var Q = require('q');
var Event = require('./Event');
var jCrop = require('jcrop');
var ScrollHelper = require('./scroll-helper');
var AutoApplySelect = require('./auto-apply-select');

var template = require('raw-loader!./temp.tpl');
var toString = Object.prototype.toString;

const quRequest = new QuRequest();
let api_photo_crop = "http://manager.1sapp.com/api/photo-crop";

/**
 * PhotoDispose 图片处理
 * @param {Element} container 包装容器
 */
function PhotoDispose(container, $message) {
    var _this = this;
    this.imgs = [];
    this.imgsMap = {};
    this.lifeCycle = {
        ready: false
    };
    this.$message = $message;

    var autoApplySelect = this.autoApplySelect = new AutoApplySelect();
    var $element = this.$element = $(template);
    var $container = this.$container = $(container);

    // var maxZIndex = getMaxZIndex($container);
    var maxZIndex = 1040;

    Event.call(this);

    $element.css('zIndex', maxZIndex)
        .hide();
    $container.append($element);

    $element.on('click', '.submit', function() {
        var $this = $(this);
        $this.prop('disabled', true).text('裁剪中');
        _this.uploderToCrop();
    });

    $element.on('click', '.close', function() {
        _this.close(true);
    });

    _this.on('uploaded', function() {
        $element.find('.submit').prop('disabled', false).text('裁剪');
    });

    _this.on('ready', function() {
        new ScrollHelper({
            $list: $element.find('.w-pd-list')
        }, {
            neglect: 80
        });
    });

    _this.applySelectHelper();
}

PhotoDispose.prototype = Object.create(Event.prototype);
PhotoDispose.constructor = PhotoDispose;

var api = PhotoDispose.prototype;

api.distory = function() {
    this.$element.remove();
};

api.enableAllJcrop = function enableAllJcrop(enable) {
    var _this = this;
    for (var key in _this.imgsMap) {
        if (enable) {
            _this.imgsMap[key].jcrop.enable();
        } else {
            _this.imgsMap[key].jcrop.disable();
        }
    }
};

/**
 * @param {string|[]} urls 图片的url
 * @param {bollean} showMe 是否显示DOM元素
 */
api.fill = function(imgs, showMe) {
    var _this = this;
    var $element = this.$element;

    imgs = imgs || [];

    if (toString.call(imgs) !== '[object Array]') imgs = [imgs];

    var countPromises = [];
    var maxWidth = window.innerWidth - 20 * 2;
    var maxHeight = window.innerHeight - 100 * 2;

    imgs.forEach(function(item, idx) {
        (function(item) {
            countPromises.push(
                countImgView(maxWidth, maxHeight, item.src)
                    .then(function(size) {
                        item.size = size;
                        return item;
                    })
                    .catch(function(reason) {
                        console.log(reason, reason.stack);
                        return reason;
                    })
            );
        })(item);
    });

    Q.all(countPromises)
        .then(function(values) {
            var errorCount = 0;
            var _values = [];
            values.forEach(function(value, idx) {
                if (value instanceof Error) {
                    errorCount++;
                } else {
                    _values.push(value);
                }
            });
            if (errorCount) {
                _this.$message.warning(`有${errorCount}张图片，因为图片无法加载导致计算图片宽高失败（可以重试一下）`);
            }
            _this.emit("ready_check");
            if (!_values.length) {
                _this.$message.warning('没有有效的图片可以进行裁剪处理');
                return;
            }

            [].push.apply(_this.imgs, _values);

            var html = "";
            _values.forEach(img => {
                html += `<li><img src="${img.src}" alt=""></li>`;
            })

            var $newItems = $(html);

            $element.find('.w-pd-list').append($newItems);
            $newItems.find('img').each(function(idx, elem) {
                var $elem = $(elem);
                var value = _values[idx];
                if (!value) { return; }
                var size = value.size;
                $elem.Jcrop({
                    boxWidth: value.size.w,
                    boxHeight: value.size.h
                }, function() {
                    // [x, y, x2, y2]
                    this.setSelect([0, 0, size.ow, size.oh]);
                    _this.imgsMap[elem.src] = {
                        jcrop: this,
                        $previewCover: $elem,
                        originImg: value.elem,
                        size: size
                    };
                });
            });

            if (showMe) _this.show();
        })
        .catch(function(reason) {
            console.log(reason, reason.stack);
        });
};

api.show = function() {
    var _this = this;
    _this.$element.show();

    if (!_this.lifeCycle.ready) {
        _this.lifeCycle.ready = true;
        setTimeout(function() {
            _this.emit('ready');
        }, 0);
    }
};

/**
 * @param {bollean} clear 是否顺便做一下清理
 */
api.close = function(clear) {
    if(clear) this.clear();
    this.$element.hide();
};

api.clear = function() {
    this.imgs = [];
    this.$element.find('.w-pd-list').empty();
};

api.uploderToCrop = function() {
    var _this = this;
    var requests = [];
    return Q.Promise(function(resolve, reject) {
        _this.imgs.forEach(function(img, idx) {
            var item = _this.imgsMap[img.src];
            var jcrop = item.jcrop;

            if (!jcrop) return;

            var area = jcrop.tellSelect();
            var size = jcrop.getBounds();
            // 宽高误差小于 2 算作没有发生裁剪
            if ( Math.abs(Math.ceil(area.w) - Math.ceil(item.size.ow)) < 2 && Math.abs(Math.ceil(area.h) - Math.ceil((item.size.oh))) < 2 ) {
                return;
            }

            requests.push({
                $previewCover: item.$previewCover,
                jcrop: jcrop,
                originImg: item.originImg,
                src: img.src,
                area: area,
                size: {
                    w: size[0],
                    h: size[1]
                }
            });
        });


        var promises = [];

        if (!requests.length) {
            resolve([]);
            return;
        }

        requests.forEach(function(item, idx) {
            (function(item) {
                promises.push(
                    Q.Promise(function(resolve, reject) {
                        $.ajax({
                            url: api_photo_crop,
                            type: 'post',
                            dataType: 'json',
                            data: {
                                token: '_token',
                                img: item.src,
                                w: item.area.w,
                                h: item.area.h,
                                x: item.area.x,
                                y: item.area.y
                            }
                        }).done(({ code, data, message }) => {
                            if (code == 0) {
                                resolve({
                                    code: 0,
                                    $previewCover: item.$previewCover,
                                    originImg: item.originImg,
                                    jcrop: item.jcrop,
                                    url: data.url,
                                    message: ''
                                });
                            } else {
                                resolve({
                                    code: -1,
                                    $previewCover: item.$previewCover,
                                    originImg: item.originImg,
                                    jcrop: item.jcrop,
                                    url: "",
                                    message: message || '处理图片失败'
                                });
                            }
                        }).fail((xhr, status, err) => {
                            resolve({
                                code: -1,
                                $previewCover: item.$previewCover,
                                originImg: item.originImg,
                                jcrop: item.jcrop,
                                url: '',
                                message: '裁剪失败: ' + err.message
                            });
                        });
                        // quRequest.send(quRequest.Apis.qukan.image.crop, {
                        //     url: item.src,
                        //     w: parseInt(item.area.w),
                        //     h: parseInt(item.area.h),
                        //     x: parseInt(item.area.x),
                        //     y: parseInt(item.area.y)
                        // }).then(({ code, data, message }) => {
                        //     if (code == 0) {
                        //         resolve({
                        //             code: 0,
                        //             $previewCover: item.$previewCover,
                        //             originImg: item.originImg,
                        //             jcrop: item.jcrop,
                        //             url: data.url,
                        //             message: ''
                        //         });
                        //     } else {
                        //         resolve({
                        //             code: -1,
                        //             $previewCover: item.$previewCover,
                        //             originImg: item.originImg,
                        //             jcrop: item.jcrop,
                        //             url: "",
                        //             message: message || '处理图片失败'
                        //         });
                        //     }
                        // }).catch(function(err) {
                        //     resolve({
                        //         code: -1,
                        //         $previewCover: item.$previewCover,
                        //         originImg: item.originImg,
                        //         jcrop: item.jcrop,
                        //         url: '',
                        //         message: '裁剪失败: ' + err.message
                        //     });
                        // });
                    })
                );
            })(item);
        });

        resolve(promises);
    }).then(function(promises) {
        return Q.all(promises)
            .then(function(records) {
                var record = {
                    success: [],
                    error: []
                };
                records.forEach(function(item, idx) {
                    if (item.code == 0) {
                        record.success.push(item);
                        //item.url
                        _this.imgs.forEach((img, idx) => {
                            if (img.src == item.originImg.src) {
                                delete _this.imgs[idx];
                            }
                        });
                    } else {
                        record.error.push(item);
                    }
                });
                _this.emit('uploaded', record);
            });
    }).catch(function(reason) {
        console.log(reason, reason.stack);
    });
};

api.applySelectHelper = function() {
    var _this = this;
    var $element = _this.$element;
    var isStartSelect = false;
    var isSelect = false;
    var selectKeys = [];
    var selectTargetKey = "";

    var $container = $('.w-pd-toolbar');

    var $start = $container.find('[data-action=start]');
    var $sure   = $container.find('[data-action=sure]');
    var $apply  = $container.find('[data-action=apply]');
    var $cancel = $container.find('[data-action=cancel]');

    $element.on('click', '.jcrop-holder', function() {
        var $this = $(this);

        if (isStartSelect) {
            selectTargetKey = $this.find('img').attr('src');
            $element.find('.jcrop-holder').removeClass('select-target');
            $this.addClass('select-target');
        }

        if (isSelect && !$this.hasClass('select-target')) {
            var key = $this.find('img').attr('src');
            var idx = selectKeys.indexOf(key);
            if (idx != -1) {
                selectKeys.splice(idx, 1);
                $this.removeClass('select');
            } else {
                selectKeys.push(key);
                $this.addClass('select');
            }

        }
    });

    $element.on('click', '.w-pd-toolbar', function(event) {
        var $elem = $(event.target);
        var action = $elem.data('action');

        switch(action) {
            case 'start':
                _this.$message('请选择一张图片作为辅助选区');
                isStartSelect = true;
                $start.hide();
                $sure.show();
                $cancel.show();
            break;
            case 'sure':
                _this.enableAllJcrop(false);
                if (!selectTargetKey) {
                    _this.$message.warning("请选择一张图片作为辅助选区");
                    return;
                }
                _this.autoApplySelect.set(_this.imgsMap[selectTargetKey]);
                isStartSelect = false;
                isSelect = true;
                $start.hide();
                $apply.show();
                $sure.hide();
                _this.$message('请选择应用的目标（可多选），若不选择则应用到全部');
            break;
            case 'apply':
                var imgs = [];
                if (!selectKeys.length) {
                    for (var key in _this.imgsMap) {
                        imgs.push(_this.imgsMap[key]);
                    }
                } else {
                    selectKeys.forEach(function(key, idx) {
                        imgs.push(_this.imgsMap[key]);
                    });
                }
                _this.autoApplySelect.apply(imgs);
                clear();
            break;
            case 'cancel':
                clear();
            break;
            default:
                throw new Error('can\'t accept action ' + action);
        }
    });

    function clear() {
        isStartSelect = false;
        isSelect = false;

        selectKeys = [];
        selectTargetKey = "";

        $start.show();
        $apply.hide();
        $sure.hide();
        $cancel.hide();

        _this.enableAllJcrop(true);

        $element.find('.jcrop-holder').removeClass('select-target select');
    }
};

function countImgView(maxWidth, maxHeight, src) {
    return Q(countImageSize(src))
        .then(function(size) {
            var w = size.w;
            var h = size.h;

            if (w > maxWidth) {
                w = maxWidth;
                h = maxWidth / size.w * size.h;
            }

            if (h > maxHeight) {
                h = maxHeight;
                w = maxHeight / size.h * w;
            }

            return {
                w: w,
                h: h,
                ow: size.w,
                oh: size.h
            };
        });
}

function countImageSize(src) {
    return Q.Promise(function(resolve, reject) {
        var img = new Image();

        img.onload = function() {
            resolve({
                w: img.width,
                h: img.height
            });
        };

        img.onerror = function() {
            reject(new Error('因为图片无法加载导致计算图片宽高失败（可以重试一下）'));
        };

        img.src = src;
    });
}

function getMaxZIndex($container) {
    var zidxs = [];
    $container.children().each(function(idx, elem) {
        var zIndex = $(elem).css('zIndex');
        if (!/\d+/.test(zIndex)) {
            zIndex = idx;
        }
        zidxs.push(zIndex);
    });
    return Math.max.apply(null, zidxs);
}

export default PhotoDispose;



